.collectionCard{
    color: white;
    background-color: #1a1c1e;
    border-radius: 10px;
    overflow: hidden;
    width: 300px;
    height: 500px;
    margin-right: 30px;
}

.collectionCard > img{
    width: 100%;
}
.wethImage{
    height: 23px;
    width: 15px;
}
.priceContainer{
    display: flex;
}
.details{
    padding: 20px;
}
.name{
    font-size: 20px;
    font-weight: 900;
}
.id{
    color: #a1a5b0;
    font-size: 18px;
    font-weight: 600;
}